* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  z-index: -1000;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
}

.background{
  min-height: 100%;
}
.app {
  width: min-content;
  height: min-content;
}
.main-container {
  z-index: 5000;
  min-height: 100vh;
  overflow: hidden;
  padding: 25px;
  text-align: center;
  color: #fff;
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
}
.flex-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100%;
}
/*Greeting Styling*/

/*Time styling*/
.time-container {
  font-size: 5rem;
  font-weight: 300;
}

/*Search bar*/
.search-box {
  width: 100%;
  margin: 0 0 35px;
  z-index: 4;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.562);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
}
.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.925);
}
/*Weather Styling*/
.location,
.weather {
  font-style: italic;
}
.weather {
  font-weight: 600;
  font-size: 1.1em;
}

/*Button Styling*/
.option {
  padding: 1em;
  margin: 0.5rem;
  border: 1px solid #fff;
  background: transparent;
  color: #fff;
}
.stats-container {
  width: max-content;
  margin: 1em auto;
  padding: 0 2em;
  border: 4px solid #fff;
  border-radius: 10px;
}
.stats-container ul {
  padding: 0;
}
.stats-container ul li {
  list-style: none;
  background-image: url();
}
